import { ConnectedRouter } from "connected-react-router";
import React from "react";
import Loadable from "react-loadable";
import { Route, Switch } from "react-router"; // react-router v4
import App from "../App";
import { Loading, Profile } from "../components";
import { Callback, Home } from "../containers";
import history from "../history";

const LoadableDftForm = Loadable({
  loader: () => import("../containers/DftForm"),
  loading: ({ error, retry, timedOut, pastDelay, isLoading }) => (
    <Loading
      error={error}
      retry={retry}
      timedOut={timedOut}
      pastDelay={pastDelay}
      isLoading={isLoading}
    />
  ),
  timeout: 10000 // 10 seconds
});
export const RootRouter = () => (
  <ConnectedRouter history={history}>
    <App>
      <Switch>
        <Route path="/profile" component={Profile} />
        <Route path="/callback" component={Callback} />
        <Route path="/submit-dft" component={LoadableDftForm} />
        <Route path="/home" component={Home} />
        <Route component={Home} />
      </Switch>
    </App>
  </ConnectedRouter>
);

export default RootRouter;



